export default (editor, opts) => {
    const { bastionModel, idContainer, idContainerFluid } = opts;

    const { idRow, labelRow } = opts;
    editor.DomComponents.addType(idRow, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelRow,
                tagName: 'div',
                attributes: {
                    class: (bastionModel.defaults.attributes.class + 'row p-0').replaceAll(/\s+/g, ' '),
                },
                droppable: true,
                draggable: (target, destination) => {
                    return destination.get('type') === idContainer || destination.get('type') === idContainerFluid || destination.get('type') === idCol;
                },
            }
        },
        isComponent: el => {
            return el.tagName === 'DIV' && el.classList.contains('row');
        },
    });

    const { idCol, labelCol } = opts;
    editor.DomComponents.addType(idCol, {
        model: {
            ...bastionModel,
            defaults: {
                ...bastionModel.defaults,
                name: labelCol,
                tagName: 'div',
                attributes: {
                    class: (bastionModel.defaults.attributes.class + ' col p-0').replaceAll(/\s+/g, ' ')
                },
                droppable: true,
                draggable: (target, destination) => {
                    return destination.get('type') === idRow;
                },

                colNum: '12',
                colSMNum: '0',
                colMDNum: '0',
                colLGNum: '0',
                colXLNum: '0',
                traits: [
                    {
                        name: 'colNum',
                        label: 'Column Number (default) | Extra Small',
                        type: 'select',
                        options: [
                            {id: '0', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colSMNum',
                        label: 'Small',
                        type: 'select',
                        options: [
                            {id: '0', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colMDNum',
                        label: 'Medium',
                        type: 'select',
                        options: [
                            {id: '0', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colLGNum',
                        label: 'Large',
                        type: 'select',
                        options: [
                            {id: '0', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                    {
                        name: 'colXLNum',
                        label: 'Extra Large',
                        type: 'select',
                        options: [
                            {id: '0', name: '0'},
                            {id: '1', name: '1'},
                            {id: '2', name: '2'},
                            {id: '3', name: '3'},
                            {id: '4', name: '4'},
                            {id: '5', name: '5'},
                            {id: '6', name: '6'},
                            {id: '7', name: '7'},
                            {id: '8', name: '8'},
                            {id: '9', name: '9'},
                            {id: '10', name: '10'},
                            {id: '11', name: '11'},
                            {id: '12', name: '12'},
                        ],
                        changeProp: 1,
                    },
                ],
                "script-prop": ['colNum', 'colSMNum', 'colMDNum', 'colLGNum', 'colXLNum'],
            },
            init() {
                this.generateCol();
                this.on('change:colNum', this.generateCol);
                this.on('change:colXSNum', this.generateCol);
                this.on('change:colSMNum', this.generateCol);
                this.on('change:colMDNum', this.generateCol);
                this.on('change:colLGNum', this.generateCol);
                this.on('change:colXLNum', this.generateCol);
            },
            generateCol(){
                let cleanedCls = this.getAttributes().class.replaceAll(/\b(col-|col-xs-|col-sm-|col-md-|col-lg-|col-xl-)([1-9]|1[0-2])\b/g, '');
                if (this.get('colNum') !== '0') cleanedCls += ` col-${this.get('colNum')}`;
                if (this.get('colXSNum') !== '0') cleanedCls += ` col-xs-${this.get('colXSNum')}`;
                if (this.get('colSMNum') !== '0') cleanedCls += ` col-sm-${this.get('colSMNum')}`;
                if (this.get('colMDNum') !== '0') cleanedCls += ` col-md-${this.get('colMDNum')}`;
                if (this.get('colLGNum') !== '0') cleanedCls += ` col-lg-${this.get('colLGNum')}`;
                if (this.get('colXLNum') !== '0') cleanedCls += ` col-xl-${this.get('colXLNum')}`;
                this.setAttributes({ class: cleanedCls.replaceAll(/\s+/g, ' ') });
            },
        },
        isComponent: el => {
            console.log('el: ', el);
            return el.tagName === 'DIV' && el.classList.contains('col');
        },
    });
}